import { useEffect, useState } from "react";
import { IoTimeOutline } from "react-icons/io5";
import { Swiper, SwiperSlide } from "swiper/react";
import { LazyLoadImage } from "react-lazy-load-image-component";
import "react-lazy-load-image-component/src/effects/black-and-white.css";
// Import Swiper styles
import "swiper/css";
import "swiper/css/navigation";

// import required modules
import ContentLoader from "react-content-loader";
import { useHistory } from "react-router-dom";
import { Navigation } from "swiper";
import http from "../../api";
import { limitText } from "../../utils";

export default function News() {
  const api = http();
  const history = useHistory();
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(true);
  useEffect(() => {
    api.LastNews(({ data }) => {
      setData(data);
      setLoading(false);
    });
  }, []);

  return (
    <div className="col-lg-12 new-car-container mt-2 p-1">
      <div className="d-flex flex-row align-items-center justify-content-between">
        <span className="title-text" style={{ fontSize: 17 }}>
          مجله و اخبار تخصصی خودرو
        </span>
        <div className="dash w-75" />
        {/* <span
          className="note-text tx-link"
          onClick={() => history.push("/news")}
        >
          مشاهده همه
        </span> */}
      </div>
      {loading ? (
        <ContentLoader
          backgroundColor={"#fff"}
          foregroundColor={"#e4e4e4e4"}
          viewBox="0 0 380 70"
        >
          {/* Only SVG shapes */}
          <rect x="0" y="0" rx="5" ry="5" width="60" height="70" />
          <rect x="70" y="0" rx="5" ry="5" width="60" height="70" />
          <rect x="150" y="0" rx="5" ry="5" width="60" height="70" />
          <rect x="230" y="0" rx="5" ry="5" width="60" height="70" />
          <rect x="310" y="0" rx="5" ry="5" width="60" height="70" />
        </ContentLoader>
      ) : (
        <Swiper
          slidesPerView={4}
          spaceBetween={30}
          modules={[Navigation]}
          // navigation={true}
          breakpoints={{
            320: {
              slidesPerView: 1.2,
              spaceBetween: 10,
            },
            // when window width is >= 480px
            480: {
              slidesPerView: 2.2,
              spaceBetween: 20,
            },
            // when window width is >= 640px
            640: {
              slidesPerView: 4.2,
              spaceBetween: 40,
            },
          }}
          className="new-car-slider-container"
        >
          {data.map((item, key) => (
            <SwiperSlide
              className="new-car-slider-item shadow"
              key={key}
              onClick={() => history.push(`/news/${item.id}/${item.href}`)}
            >
              <LazyLoadImage
                width={"100%"}
                src={`${
                  process.env.NODE_ENV === "development"
                    ? "https://localhost:7045"
                    : "https://mashinto.com"
                }/media/news/${item.featureImage}`}
                alt={item.name}
                onError={({ currentTarget }) => {
                  currentTarget.onerror = null; // prevents looping
                  currentTarget.src =
                    "https://mashinto.com/media/ads/defualt.png";
                }}
                effect="black-and-white"
                onClick={() => history.push(`/news/${item.id}/${item.href}`)}
              />

              <h1 className="title-text my-3 mx-2">
                {limitText(item.name, 45)}
              </h1>

              <div className="d-flex flex-row align-items-center justify-content-between px-2 mt-5 mb-3">
                <div className="d-flex flex-row align-items-center ">
                  <IoTimeOutline className="car-new-icon" />
                  <span className="new-car-text fw-bold">
                    {item.createdTime}
                  </span>
                </div>
                <span
                  onClick={() => history.push(`/news/${item.id}/${item.href}`)}
                  className="title-text tx-link"
                >
                  ادامه مطلب
                </span>
              </div>
            </SwiperSlide>
          ))}
        </Swiper>
      )}
    </div>
  );
}
